<template>
  <a-modal
    :title="title"
    :visible="visible"
    :keyboard="false"
    :maskClosable="false"
    okText="保存"
    okType="primary"
    @ok="handleProcessCheckNoticeOk()"
    @cancel="handleProcessCheckNoticeCancel()">
    <a-form
      :form="form">
      <a-form-item
        label="通知单编号"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'id',
            {
              rules: [
                { max:256, message: '长度在256个汉字以内' },
                {required: true, message: '请输入通知单编号'}
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.id : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="部位"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'part',
            {
              rules: [
                { max:256, message: '长度在256个汉字以内' },
                {required: true, message: '请输入部位'}
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.part : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="发出日期"
        v-bind="layout">
        <a-date-picker
          style="width:100%"
          v-decorator="[
                'give_time',
                {rules: [{type: 'object', required: true, message: '请输入发出日期'}],
                initialValue: record ? record.give_time : null}
              ]"></a-date-picker>
      </a-form-item>
      <a-form-item
        label="回复日期"
        v-bind="layout">
        <a-date-picker
          style="width:100%"
          v-decorator="[
                'reply_time',
                {rules: [],
                initialValue: record ? record.reply_time : null}
              ]"></a-date-picker>
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
import api from '../../../../../api/supervisorSecurity'
import { mapGetters } from 'vuex'
import moment from 'moment'

export default {
  name: 'ProcessNoticeModal',
  props: {
    record: Object,
    visible: Boolean,
    type: String,
  },
  data () {
    return {
      project_id: this.projectSelected().id,
      layout: {
        'label-col': { span: 8 },
        'wrapper-col': { span: 12 }
      },
      form: this.$form.createForm(this),
      matter: {}
    }
  },
  mounted(){
    this.getData()
  },
  methods: {
    ...mapGetters(['projectSelected']),
    getData(){
      if(this.isEdit){
        this.record.give_time=new moment(this.record.give_time)
        if(this.record.reply_time!==null) {
          this.record.reply_time = new moment(this.record.reply_time)
        }
      }
    },
    handleProcessCheckNoticeOk () {
      this.form.validateFields((error, data) => {
        if (!error) {
          data.give_time = data.give_time.format('YYYY-MM-DD')
          if(data.reply_time!=null) {
            data.reply_time = data.reply_time.format('YYYY-MM-DD')
          }
          if (this.isEdit) {
            api.updateProcessCheckNotice(this.record.id, data)
              .then(data => {
                this.$notification.success({ message: '成功', description: '更新成功', key: 'SUCCESS' })
                this.$emit('close')
              })
          } else {
            api.createProcessCheckNotice(this.project_id, data)
              .then(data => {
                this.$notification.success({ message: '成功', description: '新建成功', key: 'SUCCESS' })
                this.$emit('close')
              })
          }
        }
      })
    },
    handleProcessCheckNoticeCancel () {
      this.$emit('close')
    },
    validateProgressLimit (rule, value, callback) {
      if (this.isEdit) {
        callback()
      } else {
        if (value < 0 || value > 100)
          callback('false')
        else
          callback()
      }
    }
  },
  computed: {
    isEdit () {
      return this.type === 'edit'
    },
    title () {
      return this.isEdit ? '编辑' : '新建'
    }
  }
}
</script>

