<template>
  <div>
    <notice-modal
      :record="noticeModal.record"
      :visible="noticeModal.visible"
      :type="noticeModal.type"
      :table_id="noticeModal.id"
      v-if="noticeModal.visible"
      @close="handleProcessCheckNoticeClose()">
    </notice-modal>
    <a-card style="margin-bottom: 16px">
      <a-row style="margin-bottom: 5px">
        <a-col
          class="item"
          :xs="{ span: 24 }"
          :sm="{ span: 12 ,offset:12}"
          :xl="{ span: 4 ,offset:20}">
          <a-button
            type="primary"
            icon="plus"
            style="width: 100%"
            @click="handleProcessCheckNoticeCreate()">
            新建
          </a-button>
        </a-col>
      </a-row>
      <a-spin :spinning="status.listLoading">
        <a-table
          bordered
          :columns="columns"
          :dataSource="matterList"
          rowKey="id"
          :pagination="false">
          <template slot="notice_letter" slot-scope="text, record, index">
            <a-button @click="$router.push({'name': 'processCheckNoticeFileDetail', params: {id: record.id}})">
              文件管理
            </a-button>
          </template>
          <template slot="reply_letter" slot-scope="text, record, index">
            <a-button @click="$router.push({'name': 'processCheckReplyFileDetail', params: {id: record.id}})">
              文件管理
            </a-button>
          </template>
          <template slot="operation" slot-scope="text, record, index">
            <a-button @click="handleProcessCheckNoticeEdit(record)">编辑</a-button>
            <a-popconfirm
              title="确认删除吗?"
              @confirm="handleProcessCheckNoticeDelete(record)">
              <a-button type="danger">删除</a-button>
            </a-popconfirm>
          </template>
        </a-table>
      </a-spin>
      <a-col
        :xs="{span: 24}"
        :sm="{span: 20, offset: 2}"
        :xl="{span: 16, offset: 4}">
      </a-col>
    </a-card>
  </div>
</template>

<script>
import NoticeModal from './components/NoticeModal'
import api from '../../../../api/supervisorSecurity'
import { mapGetters } from 'vuex'
import ACol from 'ant-design-vue/es/grid/Col'

const columns = [
  {
    title: '通知单编号',
    dataIndex: 'id',
    width: '6%',
    align: 'center'
  },
  {
    title: '部位',
    dataIndex: 'part',
    width: '9%',
    align: 'center'
  },
  {
    title: '发出日期',
    dataIndex: 'give_time',
    width: '16%',
    align: 'center'
  },
  {
    title: '回复日期',
    dataIndex: 'reply_time',
    width: '16%',
    align: 'center'
  },
  {
    title: '通知单',
    dataIndex: 'notice_letter',
    scopedSlots: { customRender: 'notice_letter' },
    width: '20%',
    align: 'center'
  },
  {
    title: '回复单',
    dataIndex: 'reply_letter',
    scopedSlots: { customRender: 'reply_letter' },
    width: '20%',
    align: 'center'
  },
  {
    title: '编辑',
    dataIndex: 'operation',
    scopedSlots: { customRender: 'operation' },
    align: 'center'
  }]
export default {
  name: 'processCheckNotice',
  components: {
    ACol,
    NoticeModal
  },
  props: {
    id: Number
  },
  data () {
    return {
      status: {
        listLoading: true,
        tableLoading: true
      },
      layout: {
        'label-col': { span: 6 },
        'wrapper-col': { span: 18 }
      },
      form: this.$form.createForm(this),
      noticeModal: {
        record: null,
        visible: false,
        type: '1',
        id: this.id
      },
      columns,
      matterList: [],
      project_id: this.projectSelected().id,
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    ...mapGetters(['projectSelected']),
    getData () {
      this.status.listLoading = false
      api.getProcessCheckNoticeList(this.project_id)
        .then(data => {
          this.matterList = data
          this.status.listLoading = false
        })
    },
    handleProcessCheckNoticeClose () {
      this.noticeModal.type = ''
      this.noticeModal.record = null
      this.noticeModal.visible = false
      this.getData()
    },
    handleProcessCheckNoticeCreate () {
      this.noticeModal.type = 'create'
      this.noticeModal.record = null
      this.noticeModal.visible = true
    },
    handleProcessCheckNoticeEdit (record) {
      this.noticeModal.type = 'edit'
      this.noticeModal.record = record
      this.noticeModal.visible = true
    },
    handleProcessCheckNoticeDelete (record) {
      api.deleteProcessCheckNotice(record.id)
        .then(data => {
          this.$notification.success({ message: '成功', description: '删除成功', key: 'SUCCESS' })
        })
    }
  }
}
</script>

<style scoped>
  .test {
    color: #c82333;
  }
</style>