<template>
    <div>
      <process-detail-modal
      :record="modal.record"
      :visible="modal.visible"
      :type="modal.type"
      :detail_id="modal.detail_id"
      v-if="modal.visible"
      @close="handleClose()">
      </process-detail-modal>
        <a-card style="margin-bottom: 16px">
      <a-row class="item">
        <a-col
          :xs="{ span: 24 }"
          :sm="{ span: 12, offset: 12 }"
          :xl="{ span: 4, offset: 20 }">
          <a-button
            type="primary"
            icon="plus"
            style="float: right; margin-right: 5px"
            @click="handleCreate()">
            新建
          </a-button>
           <a-button
            style="float: right; margin-right: 5px;"
            @click="$router.go(-1)">
            返回
          </a-button>
        </a-col>

      </a-row>
      <a-table
        bordered
        :columns="columns"
        :dataSource="detailList"
        rowKey="id"
        :pagination="false">
        <template slot="operation" slot-scope="text, record, index">
          <a-button @click="handleEdit(record)">编辑</a-button>
          <a-popconfirm
            title="确认删除吗?"
            @confirm="handleDelete(record)">
            <a-button type="danger">删除</a-button>
          </a-popconfirm>
        </template>
      </a-table>
    </a-card>
    </div>
</template>

<script>
  const columns = [
    {
      title: '投资单位名称',
      dataIndex: 'name',
      width: '12%',
      align: 'center'
    },
    {
      title: '支付时间',
      dataIndex: 'time',
      width: '12%',
      align: 'center'
    },
    {
      title: '本次实付',
      dataIndex: 'money',
      width: '12%',
      align: 'center'
    },
    {
      title: '编辑',
      dataIndex: 'operation',
      scopedSlots: { customRender: 'operation' },
      align: 'center'
    }
  ]
  import ProcessDetailModal from './components/processDetailModal'
  import api from '../../../../api/implement'
  export default {
    name: 'AccountProcessDetail',
    components: { ProcessDetailModal },
    props: {
      id: Number
    },
    data () {
      return {
        layout: {
          'label-col': { span: 6 },
          'wrapper-col': { span: 18 }
        },
        status:{
          loading:true
        },
        form: this.$form.createForm(this),
        details:{},
        modal: {
          record: null,
          visible: false,
          type: '1',
          detail_id: this.id
        },
        columns,
        detailList: []
      }
    },
    mounted () {
      this.getData()
    },
    methods: {

      getData () {
        this.status.loading=true
        api.getInvestProcessDetailList(this.id)
          .then(data => {

            this.name=data.name
            this.detailList = data.child
            this.status.loading=false
          })
        //this.$store.getters.projectSelected.id
        api.getInvestProcessDetail(this.id)
          .then(data => {
            console.log(data)
            this.details = data
          })
      },
      handleClose () {
        this.modal.type = ''
        this.modal.record = null
        this.modal.visible = false
        this.getData()
      },
      handleCreate () {
        this.modal.type = 'create'
        this.modal.record = null
        this.modal.visible = true
      },
      handleEdit (record) {
        this.modal.type = 'edit'
        this.modal.record = record
        this.modal.visible = true
      },
      handleDelete (record) {
        api.deleteInvestProcessDetail(record.id)
          .then(data => {
            this.$notification.success({ message: '成功', description: '删除成功', key: 'SUCCESS' })
            this.getData()
          })
      },
      parseFloat (f) {
        return parseFloat(f)
      }
    }
  }
</script>

