<template>
  <a-modal
    :title="title"
    :visible="visible"
    :keyboard="false"
    :maskClosable="false"
    okText="保存"
    okType="primary"
    @ok="handleProcessAuditOk()"
    @cancel="handleProcessAuditCancel()">
    <a-form
      :form="form">
      <a-form-item
        label="名称"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'name',
            {
              rules: [
                { max:64, message: '长度在64个汉字以内' },
                {required: true, message: '请输入名称'}
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.name : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="是否合格"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'review_status',
            {
              rules: [
                { max:32, message: '长度在32个汉字以内' }
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.review_status : null
            }
          ]">
        </a-input>
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
import api from '../../../../../api/supervisorQuality'
import { mapGetters } from 'vuex'

export default {
  name: 'ProcessAuditModal',
  props: {
    record: Object,
    visible: Boolean,
    type: String,
    process_review_id:Number
  },
  data () {
    return {
      project_id: this.projectSelected().id,
      layout: {
        'label-col': { span: 8 },
        'wrapper-col': { span: 12 }
      },
      form: this.$form.createForm(this),
      matter: {}
    }
  },
  methods: {
    ...mapGetters(['projectSelected']),
    handleProcessAuditOk () {
      console.log(this.matter_id)
      this.form.validateFields((error, data) => {
        if (!error) {
          if (this.isEdit) {
            api.updateProcessAuditTable(this.record.matter_id, data)
              .then(data => {
                this.$notification.success({ message: '成功', description: '更新成功', key: 'SUCCESS' })
                this.$emit('close')
              })
          } else {
            api.createProcessAuditTable(this.process_review_id, data)
              .then(data => {
                this.$notification.success({ message: '成功', description: '新建成功', key: 'SUCCESS' })
                this.$emit('close')
                console.log(this.process_review_id)
              })
          }
        }
      })
    },
    handleProcessAuditCancel () {
      this.$emit('close')
    },
    validateProgressLimit (rule, value, callback) {
      if (this.isEdit) {
        callback()
      } else {
        if (value < 0 || value > 100)
          callback('false')
        else
          callback()
      }
    }
  },
  computed: {
    isEdit () {
      return this.type === 'edit'
    },
    title () {
      return this.isEdit ? '编辑' : '新建'
    }
  }
}
</script>

