<template>
  <div>
    <process-audit-modal
      :record="auditModal.record"
      :visible="auditModal.visible"
      :type="auditModal.type"
      :process_review_id="auditModal.process_review_id"
      v-if="auditModal.visible"
      @close="handleProcessAuditClose()">
    </process-audit-modal>
    <a-card style="margin-bottom: 16px">
      <a-row style="margin-bottom: 5px">
        <a-col
          class="item"
          :xs="{ span: 24 }"
          :sm="{ span: 12 ,offset:12}"
          :xl="{ span: 4 ,offset:20}">
          <a-button
            type="primary"
            icon="plus"
            style="width: 100%"
            @click="handleProcessAuditCreate()">
            新建
          </a-button>
        </a-col>
      </a-row>
      <a-spin :spinning="status.listLoading">
        <a-table
          bordered
          :columns="columns"
          :dataSource="matterList"
          rowKey="matter_id"
          :pagination="false">
          <template slot="id" slot-scope="text, record, index">
            <div>{{index+1}}</div>
          </template>
          <template slot="content" slot-scope="text, record, index">
            <a-button @click="$router.push({'name': 'processAuditDetail', params: {id: record.matter_id}})">
              文件管理
            </a-button>
          </template>
          <template slot="operation" slot-scope="text, record, index">
            <a-button @click="handleProcessAuditEdit(record)">编辑</a-button>
            <a-popconfirm
              title="确认删除吗?"
              @confirm="handleProcessAuditDelete(record)">
              <a-button type="danger">删除</a-button>
            </a-popconfirm>
          </template>
        </a-table>
      </a-spin>
      <a-col
        :xs="{span: 24}"
        :sm="{span: 20, offset: 2}"
        :xl="{span: 16, offset: 4}">
      </a-col>
    </a-card>
  </div>
</template>

<script>
import ProcessAuditModal from './components/ProcessAuditModal'
import api from '../../../../api/supervisorQuality'
import { mapGetters } from 'vuex'
import ACol from 'ant-design-vue/es/grid/Col'

const columns = [
  {
    title: '序号',
    dataIndex: 'id',
    scopedSlots: { customRender: 'id' },
    width: '15%',
    align: 'center'
  },
  {
    title: '名称',
    dataIndex: 'name',
    width: '25%',
    align: 'center'
  },
  {
    title: '申报内容',
    dataIndex: 'content',
    scopedSlots: { customRender: 'content' },
    width: '20%',
    align: 'center'
  },
  {
    title: '是否合格',
    dataIndex: 'review_status',
    width: '20%',
    align: 'center'
  },
  {
    title: '编辑',
    dataIndex: 'operation',
    scopedSlots: { customRender: 'operation' },
    align: 'center'
  }]
export default {
  name: 'ProcessAuditTable',
  components: {
    ACol,
    ProcessAuditModal
  },
  props: {
    id: String
  },
  data () {
    return {
      status: {
        listLoading: true
      },
      layout: {
        'label-col': { span: 6 },
        'wrapper-col': { span: 18 }
      },
      form: this.$form.createForm(this),
      auditModal: {
        record: null,
        visible: false,
        type: '1',
        process_review_id: this.id
      },
      columns,
      matterList: [],
      project_id: this.projectSelected().id,
      name:''
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    ...mapGetters(['projectSelected']),
    getData () {
      this.status.listLoading = false
      this.process_review_id=this.id
      console.log(this.id)
      console.log(this.process_review_id)
      api.getProcessAuditTableList(this.id)
        .then(data => {
          this.matterList = data
          this.status.listLoading = false
        })
    },
    handleProcessAuditClose () {
      this.auditModal.type = ''
      this.auditModal.record = null
      this.auditModal.visible = false
      this.getData()
    },
    handleProcessAuditCreate () {
      this.auditModal.type = 'create'
      this.auditModal.record = null
      this.auditModal.visible = true
    },
    handleProcessAuditEdit (record) {
      this.auditModal.type = 'edit'
      this.auditModal.record = record
      this.auditModal.visible = true
    },
    handleProcessAuditDelete (record) {
      api.deleteProcessAuditTable(record.matter_id)
        .then(data => {
          this.$notification.success({ message: '成功', description: '删除成功', key: 'SUCCESS' })
        })
    }
  }
}
</script>

<style scoped>
  .test {
    color: #c82333;
  }
</style>