<template>
  <a-modal
    :title="title"
    :visible="visible"
    :keyboard="false"
    :maskClosable="false"
    okText="保存"
    okType="primary"
    @ok="handleOk()"
    @cancel="handleCancel()">
    <a-form
      :form="form">
      <a-form-item
        label="日期"
        :label-col="{span: 8}"
        :wrapper-col="{span: 12}"
        v-bind="layout">
        <a-date-picker
          style="width:100%"
          v-decorator="[
                'patrol_date',
                {rules: [{type: 'object', required: true, message: '请输入日期'}],
              initialValue: record ? record.patrol_date : null}
              ]"></a-date-picker>
      </a-form-item>
      <a-form-item
        label="施工安全生产"
        :label-col="{span: 8}"
        :wrapper-col="{span: 12}">
        <a-input
          type="text"
          v-decorator="[
            'security_production',
            {
              rules:[
                { max:32, message: '长度在32个汉字以内' }
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.security_production : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="安全设施配备"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'security_facilities',
            {
              rules: [
                { max:32, message: '长度在32个汉字以内' }
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.security_facilities : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="安全隐患"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
          'security_hidden_danger',
          {
            rules: [
              { max:32, message: '长度在32个汉字以内' }
            ],
            validateTrigger: 'blur',
            initialValue: record ? record.security_hidden_danger : null
          }
        ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="前期问题整改"
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'previous_problems',
            {
              rules: [
                { max:32, message: '长度在32个字符以内' }
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.previous_problems : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label="检查人"
        v-bind="layout">
       <a-input
          type="text"
          v-decorator="[
            'inspector',
            {
              rules: [
               { required: true, message: '请输入检查人' },
                { max:32, message: '长度在32个字符以内' }
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.inspector : null
            }
          ]">
        </a-input>
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
  import api from '../../../../../api/supervisorSecurity'
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    name: 'SecurityPatrolModal',
    props: {
      record: Object,
      visible: Boolean,
      type: String,
    },
    data () {
      return {
        project_id:this.projectSelected().id,
        layout: {
          'label-col': { span: 8 },
          'wrapper-col': { span: 12 }
        },
        form: this.$form.createForm(this),
        matter: {}
      }
    },
    mounted () {
      this.getData()
    },
    methods: {
       ...mapGetters(['projectSelected']),
       getData () {
         if (this.isEdit) {
           this.record.patrol_date = new moment(this.record.patrol_date)
           console.log(this.record.patrol_date)
         }
       },
      handleOk () {
        this.form.validateFields((error, data) => {
          if (!error) {
            data.patrol_date = data.patrol_date.format('YYYY-MM-DD')
            if (this.isEdit) {
              api.updateSecurityPatrol(this.record.security_patrol_id, data)
                .then(data => {
                  this.$notification.success({message: '成功', description: '更新成功', key: 'SUCCESS'})
                  this.$emit('close')
                })
            } else {
              api.createSecurityPatrol(this.project_id,data)
                .then(data => {
                  this.$notification.success({message: '成功', description: '新建成功', key: 'SUCCESS'})
                  this.$emit('close')
                })
            }
          }
        })
      },
      handleCancel () {
        this.$emit('close')
      }
    },
    computed: {
      isEdit () {
        return this.type === 'edit'
      },
      title () {
        return this.isEdit ? '编辑' : '新建'
      }
    }
  }
</script>

<style scoped lang="less">

</style>