<template>
  <div>
    <security-patrol-modal
      :record="modal.record"
      :visible="modal.visible"
      :type="modal.type"
      :bid_id="modal.security_patrol_id"
      v-if="modal.visible"
      @close="handleClose()">
    </security-patrol-modal>
    <a-card style="margin-bottom: 16px">
      <a-row>
        <a-col
          class="item"
          :xs="{ span: 24 }"
          :sm="{ span: 12 }"
          :xl="{ span: 4 }">
          <a-button
            type="primary"
            icon="plus"
            style="width: 100%"
            @click="handleCreate()">
            新建
          </a-button>
        </a-col>
      </a-row>
      <a-spin :spinning="status.listLoading">
        <a-table
          bordered
          :columns="columns"
          :dataSource="matterList"
          rowKey="security_patrol_id"
          :pagination="false">
          <template slot="operation" slot-scope="text, record, index">
            <a-button @click="handleEdit(record)">编辑</a-button>
            <a-popconfirm
              title="确认删除吗?"
              @confirm="handleDelete(record)">
              <a-button type="danger">删除</a-button>
            </a-popconfirm>
          </template>
        </a-table>
      </a-spin>
    </a-card>
  </div>
</template>

<script>
  import SecurityPatrolModal from './components/SecurityPatrolModal'
  import api from '../../../../api/supervisorSecurity'
  import {mapGetters} from 'vuex'

  const columns = [
    {
      title: '日期',
      dataIndex: 'patrol_date',
      width: '10%',
      align: 'center'
    },
    {
      title: '施工安全生产',
      dataIndex: 'security_production',
      width: '17%',
      align: 'center'
    }, {
      title: '安全设施配备',
      dataIndex: 'security_facilities',
      width: '17%',
      align: 'center'
    },
    {
      title: '安全隐患',
      dataIndex: 'security_hidden_danger',
      width: '17%',
      align: 'center'
    },
    {
      title: '前期问题整改',
      dataIndex: 'previous_problems',
      width: '17%',
      align: 'center'
    },
    {
      title: '检查人',
      dataIndex: 'inspector',
      width: '12%',
      align: 'center'
    },
    {
      title: '编辑',
      dataIndex: 'operation',
      scopedSlots: {customRender: 'operation'}
    }
  ]

  export default {
    name: 'SecurityPatrol',
    components: {
      SecurityPatrolModal
    },
    props: {
      id: Number
    },
    data() {
      return {
        status: {
          listLoading: true,
          tableLoading: true
        },
        layout: {
          'label-col': {span: 6},
          'wrapper-col': {span: 18}
        },
        form: this.$form.createForm(this),
        modal: {
          record: null,
          visible: false,
          type: '1',
          security_patrol_id: this.id
        },
        columns,
        matterList: [],
        project_id: this.projectSelected().id
      }
    },
    mounted() {
      this.getData()
    },

    methods: {
      ...mapGetters(['projectSelected']),
      getData() {
        this.status.listLoading = true
        api.getSecurityPatrolList(this.project_id)
          .then(data => {
            this.matterList = data
            this.status.listLoading = false
          })
      },
      handleClose() {
        this.modal.type = ''
        this.modal.record = null
        this.modal.visible = false
        this.getData()
      },
      handleCreate() {
        this.modal.type = 'create'
        this.modal.record = null
        this.modal.visible = true
      },
      handleEdit(record) {
        this.modal.type = 'edit'
        this.modal.record = record
        this.modal.visible = true
      },
      handleDelete(record) {
        api.deleteSecurityPatrol(record.security_patrol_id)
          .then(data => {
            this.$notification.success({message: '成功', description: '删除成功', key: 'SUCCESS'})
          })
      },
      parseFloat(f) {
        return parseFloat(f)
      }
    }
  }
</script>

<style scoped lang="less">
  .item {
    margin-bottom: 10px;
  }

  .wrapper {
    width: 7rem;
    height: 5rem;
  }
</style>