<template>
  <div>
    <risk-list-modal
      :record="riskModal.record"
      :visible="riskModal.visible"
      :type="riskModal.type"
      :table_id="riskModal.id"
      v-if="riskModal.visible"
      @close="handleRiskListClose()">
    </risk-list-modal>
    <a-card style="margin-bottom: 16px">
      <a-row style="margin-bottom: 5px">
        <a-col
          class="item"
          :xs="{ span: 24 }"
          :sm="{ span: 12 ,offset:12}"
          :xl="{ span: 4 ,offset:20}">
          <a-button
            type="primary"
            icon="plus"
            style="width: 100%"
            @click="handleRiskListCreate()">
            新建
          </a-button>
        </a-col>
      </a-row>
      <a-spin :spinning="status.listLoading">
        <a-table
          bordered
          :columns="columns"
          :dataSource="matterList"
          rowKey="id"
          :pagination="false">
          <template slot="risk_id" slot-scope="text, record, index">
            <div>{{index+1}}</div>
          </template>
          <template slot="files" slot-scope="text, record, index">
            <a-button @click="$router.push({'name': 'RiskListDetail', params: {id: record.id}})">
              附件管理
            </a-button>
          </template>
          <template slot="operation" slot-scope="text, record, index">
            <a-button @click="handleRiskListEdit(record)">编辑</a-button>
            <a-popconfirm
              title="确认删除吗?"
              @confirm="handleRiskListDelete(record)">
              <a-button type="danger">删除</a-button>
            </a-popconfirm>
          </template>
        </a-table>
      </a-spin>
      <a-col
        :xs="{span: 24}"
        :sm="{span: 20, offset: 2}"
        :xl="{span: 16, offset: 4}">
      </a-col>
    </a-card>
  </div>
</template>

<script>
import RiskListModal from './components/RiskListModal'
import api from '../../../api/supervisorSecurity'
import { mapGetters } from 'vuex'
import ACol from 'ant-design-vue/es/grid/Col'
import ARow from 'ant-design-vue/es/grid/Row'

const columns = [
  {
    title: '序号',
    dataIndex: 'risk_id',
    scopedSlots: { customRender: 'risk_id' },
    width: '10%',
    align: 'center'
  },
  {
    title: '内容',
    dataIndex: 'risk_content',
    width: '20%',
    align: 'center'
  },
  {
    title: '部位',
    dataIndex: 'part',
    width: '15%',
    align: 'center'
  },
  {
    title: '附件',
    dataIndex: 'files',
    scopedSlots: { customRender: 'files' },
    width: '15%',
    align: 'center'
  },
  {
    title: '风险等级',
    dataIndex: 'risk_rank',
    width: '18%',
    align: 'center'
  },
  {
    title: '编辑',
    dataIndex: 'operation',
    scopedSlots: { customRender: 'operation' },
    align: 'center'
  }]
export default {
  name: 'RiskList',
  components: {
    ARow,
    ACol,
    RiskListModal
  },
  props: {
    id: Number
  },
  data () {
    return {
      status: {
        listLoading: true,
        tableLoading: true
      },
      layout: {
        'label-col': { span: 6 },
        'wrapper-col': { span: 18 }
      },
      form: this.$form.createForm(this),
      riskModal: {
        record: null,
        visible: false,
        type: '1',
        id: this.id
      },
      columns,
      matterList: [],
      project_id: this.projectSelected().id,
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    ...mapGetters(['projectSelected']),
    getData () {
      this.status.listLoading = false
      api.getRiskList(this.project_id)
        .then(data => {
          this.matterList = data
          this.status.listLoading = false
        })
    },
    handleRiskListClose () {
      this.riskModal.type = ''
      this.riskModal.record = null
      this.riskModal.visible = false
      this.getData()
    },
    handleRiskListCreate () {
      this.riskModal.type = 'create'
      this.riskModal.record = null
      this.riskModal.visible = true
    },
    handleRiskListEdit (record) {
      this.riskModal.type = 'edit'
      this.riskModal.record = record
      this.riskModal.visible = true
    },
    handleRiskListDelete (record) {
      api.deleteRiskList(record.id)
        .then(data => {
          this.$notification.success({ message: '成功', description: '删除成功', key: 'SUCCESS' })
        })
    }
  }
}
</script>

<style scoped>
  .test {
    color: #c82333;
  }
</style>